import React from 'react'
import img_m_p from '../assets/img/Icon propietario M.svg'
import icon_editar from '../assets/img/Icon pencil.svg'
import icon_ver_mas from '../assets/img/Icon Ver m치s.svg'

const CardContacto = ({nombre,sexo,t1,t2,email,status,t_contacto}) => {

    let status_text = ''
    let status_style = ''
    let fontSize = '10px'
    switch (status) {
        case 1:
            status_text ='ACTIVO'
            status_style ='btn-info'
            fontSize = '13px'
            break;
        case 2:
            status_text ='EN PROCESO'  
            status_style ='btn-cafe' 
            fontSize = '9px'         
            break;
        case 3:
            status_text ='SIN CONTACTAR'
            status_style ='btn-negro'
            fontSize = '8px'
            break;
    
        default:
            break;
    }

    return (
        <div className="col-12 my-3">
            <div className="card bandin-card text-center">
                <div className="card-header">
                    {nombre}
                </div>
                <div className="card-body">
                    <div className="row">
                        <div className="col-6 col-lg-1">
                            <img src={img_m_p} alt="propietario"/>
                        </div>
                        <div className="col-6 col-lg-4" style={{fontSize:'10px'}}>
                            <p>{t1}</p>
                            <p>{t2}</p>
                            <p>{email}</p>
                        </div>
                        <div className="col-lg-3 xs-d-none">
                            <span>hey</span>
                        </div>
                        <div className="col-lg-4 d-flex justify-content-between align-items-center xs-d-none sm-d-none">
                                <button className={`btn ${status_style}`} style={{ fontSize}}>
                                    {status_text}
                                </button>
                                <button className="btn btn-outline-info">
                                    <img src={icon_editar} alt="editar" style={{width:'20px', height:'20px', textAlign:'center'}}/>
                                </button>
                                <button className="btn btn-outline-info">
                                    <img src={icon_ver_mas} alt="ver mas" style={{width:'20px', height:'20px', textAlign:'center'}}/>
                                </button>
                        </div>
                    </div>
                </div>
                <div className="card-footer ">
                    <div className="row">
                        <div className="col-12 d-flex justify-content-between lg-d-none">
                            <button className={`btn ${status_style}`} style={{maxWidth:'86.83px', fontSize}}>
                                {status_text}
                            </button>
                            <button className="btn btn-outline-info">
                                <img src={icon_editar} alt="editar" style={{width:'20px', height:'20px', textAlign:'center'}}/>
                            </button>
                            <button className="btn btn-outline-info">
                                <img src={icon_ver_mas} alt="ver mas" style={{width:'20px', height:'20px', textAlign:'center'}}/>
                            </button>
                        </div>
                    </div>
                    
                </div>
            </div>
        </div>
    )
}

export default CardContacto
